@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700&display=swap');

#carouselExampleIndicators {
    width: 80%;
    margin: 20px auto; 
    max-height: 500px;
    overflow: hidden; 
}

#carouselExampleIndicators .carousel-inner img {
    width: 100%;
    height: 100%;
    object-fit: cover; 
    object-position: center; 
}

:root {
	--color-fire: #ff7402;
	--color-grass: #9bcc50;
	--color-steel: #9eb7b8;
	--color-water: #4592c4;
	--color-psychic: #f366b9;
	--color-ground: #ab9842;
	--color-ice: #51c4e7;
	--color-flying: #3dc7ef;
	--color-ghost: #4d5b64;
	--color-normal: #a4acaf;
	--color-poison: #7e0058;
	--color-rock: #a38c21;
	--color-fighting: #d56723;
	--color-dark: #707070;
	--color-bug: #729f3f;
	--color-dragon: linear-gradient(180deg, #53a4cf 50%, #f16e57 50%);
	--color-electric: #bba909;
	--color-fairy: #fdb9e9;
	--color-shadow: #7b62a3;
	--color-unknow: #757575;
	--color-primary: #006d77;
	--color-primary-hover: #83c5be;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Montserrat';
}

.container {
	max-width: 1200px;
	margin: 0 auto;
}

header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 40px 0;
}

.logo img {
	width: 180px;
}

header form {
	display: flex;
	align-items: center;

	gap: 15px;
}

.form-group {
	display: flex;
	align-items: center;
	gap: 10px;
	margin-bottom: 0;
	border: 1px solid #555;
	padding: 15px 20px;
	border-radius: 25px;
}

.form-group input {
	font-family: inherit;
	width: 300px;
	border: none;
	outline: none;
	font-size: 15px;
}

.icon-search {
	width: 20px;
	height: 20px;
	stroke: #555;
}

.btn-search {
	border: none;
	outline: none;
	border-radius: 25px;
	padding: 15px 30px;
	font-family: inherit;
	color: #fff;
	background-color: var(--color-primary);
	cursor: pointer;
	font-weight: 500;
	letter-spacing: 2px;
}

.btn-search:hover {
	background-color: var(--color-primary-hover);
	color: #000;
}

.container-filter {
	display: flex;
}

.icon-filter {
	display: flex;
	align-items: center;
	gap: 15px;
	margin-bottom: 20px;
	cursor: pointer;
}

.icon-filter .icon {
	width: 30px;
	height: 30px;
	stroke: #555;
}

.card-list-pokemon {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 20px;
}

.card-pokemon {
	cursor: pointer;
	text-decoration: none;
}

.card-img {
	background-color: #f2f2f2;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 5px;
	height: 250px;
}

.card-img img {
	width: 100%;
	height: 100%;
}

.card-info {
	padding: 15px;
}

.card-info h3 {
	color: #333;
}

.pokemon-id {
	display: block;
	color: #888;
	margin-bottom: 15px;
}

.card-types {
	display: flex;
	gap: 10px;
	margin-top: 10px;
}

.card-types span {
	font-size: 12px;
	padding: 5px 20px;
	border-radius: 5px;
	color: #fff;
}

.water {
	background-color: var(--color-water);
}
.grass {
	background-color: var(--color-grass);
}
.poison {
	background-color: var(--color-poison);
}
.fire {
	background-color: var(--color-fire);
}
.bug {
	background-color: var(--color-bug);
}
.dragon {
	background: var(--color-dragon);
}
.flying {
	background-color: var(--color-flying);
}
.ground {
	background-color: var(--color-ground);
}
.steel {
	background-color: var(--color-steel);
}
.psychic {
	background-color: var(--color-psychic);
}
.ice {
	background-color: var(--color-ice);
}
.ghost {
	background-color: var(--color-ghost);
}
.normal {
	background-color: var(--color-normal);
}
.rock {
	background-color: var(--color-rock);
}
.electric {
	background-color: var(--color-electric);
}
.fighting {
	background-color: var(--color-fighting);
}
.fairy {
	background-color: var(--color-fairy);
}
.dark {
	background-color: var(--color-dark);
}

.container-filters {
	display: flex;
	justify-content: center;
	position: fixed;
	top: 0;
	left: -300px;
	width: 250px;
	color: #555;
	background-color: #f2f2f2;
	height: 100%;
	padding-top: 50px;
	transition: all 0.3s linear;
}

.container-filters.active {
	left: 0;
}

.filter-by-type {
	display: flex;
	flex-direction: column;
	gap: 5px;
}

.filter-by-type span {
	font-weight: 700;
	font-size: 22px;
}

.word-type{
	margin-top: 10px ;
}

.group-type {
	display: flex;
	gap: 10px;
	margin-left: 15px;
}

.group-type label {
	cursor: pointer;
}

.main-pokemon {
	display: flex;
	flex-direction: column;
	margin-top: 100px;
	margin-bottom: 50px;
}

.header-main-pokemon {
	margin-top: 20px;
	display: flex;
	align-items: center;
	position: relative;
}

.number-pokemon {
	position: absolute;
	top: -130px;
	left: 0;
	font-size: 200px;
	font-weight: 700;
	color: var(--color-primary-hover);
}

.container-img-pokemon {
	order: 2;
	flex: 1;
	padding: 25px 25px 25px 0;
	height: 400px;
}

.container-img-pokemon img {
	height: 100%;
	width: 100%;
}

.container-info-pokemon {
	display: flex;
	flex-direction: column;
	order: 1;
	flex: 1;
}

.container-info-pokemon h1 {
	text-align: right;
	font-size: 60px;
}

.info-pokemon {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
}

.group-info{
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.group-info p {
	font-weight: 700;
	font-size: 20px;
}

.container-stats {
	display: flex;
	gap: 50px;
	align-items: center;
}

.stats {
	display: flex;
	flex-direction: column;
	gap: 20px;
	flex: 1;
}

.stat-group {
	display: flex;
	align-items: center;
	gap: 20px;
}

.stat-group span {
	flex: 20%;
	font-weight: 500;
	font-size: 18px;
}

.progress-bar {
	width: 100%;
	height: 30px;
	background-color: var(--color-primary);
	border-radius: 10px;
}

.p-search{
	margin-bottom: 30px;
	font-size: 22px;
}

.p-search span{
	font-weight: 700;
}

.container-loader{
	display: flex;
	justify-content: center;
	margin: 200px 0;
}

.container-btn-load-more{
	display: flex;
	justify-content: center;
	margin-bottom: 20px;
	margin-top: 20px;
}

.btn-load-more{
	border: none;
	padding: 15px 40px;
	font-family: inherit;
	background-color: var(--color-primary);
	color: #fff;
	border-radius: 10px;
	cursor: pointer;
}

.btn-load-more:hover{
	background-color: var(--color-primary-hover);
	color: #000;
}

@media (max-width: 851px) {
    .container {
        max-width: 500px;
        margin: auto;
    }
    header { padding: 10px 0; display: block; }
    .logo img { width: 150px; margin: 0 30% 15px; }
    .form-group {
        display: flex;
        align-items: center;
        gap: 2px;
        padding: 8px;
        border-radius: 20px;
        margin-left: 20px;
    }
    .form-group input { width: 200px; font-size: 10px; }
    .btn-search { font-weight: 10px; padding: 8px; border-radius: 20px; }
    .icon-filter .icon, .card-img img { width: 70px; height: 70px; }
    .menu-type, .filter-by-type span, .word-type, .stat-group span { font-size: 15px; }
    .card-list-pokemon { display: grid; gap: 20px; grid-template-columns: 1fr; }
    .card-img { height: 150px; }
    .card-info h3 { font-size: 20px; padding: 5px; }
    .container-filters { left: -200px; width: 150px; }
    .filter-by-type { gap: 0; }
    .number-pokemon { top: -45px; font-size: 50px; }
    .header-main-pokemon { margin-top: 20px; display: grid; }
    .container-img-pokemon { padding: 10px; height: 200px; width: 300px; margin-left: 30px; }
    .container-img-pokemon img { height: 80%; }
    .info-pokemon { display: flex; justify-content: space-around; margin-top: 20px; }
    .container-info-pokemon h1 { font-size: 30px; margin-right: 100px; }
    .container-stats { display: grid; gap: 0; margin-bottom: 20px; }
    .card-types { margin-left: 99px; }
    .progress-bar { width: 50%; height: 20px; }
    .h1, h1 { font-size: 1.8rem; }
}